<script>
    import DatePicker from "./DatePicker.svelte";
    import Table, { Pagination, Row, Search, Sort } from "./Table.svelte";
    import { onMount } from 'svelte';
    import {Form, FormGroup, Input,Label ,Button,Modal,
    ModalBody,
    ModalFooter,
    ModalHeader} from 'sveltestrap'
    import { sortNumber, sortString } from "./sorting.js";
    import { Circle } from 'svelte-loading-spinners'
  
    let currentDate = new Date();

    let page = 0;
    let pageSize = 10;
    let prows = [];
    let selected = null;
    let attendance = false;
  
    const onDateChange = d => {
      currentDate = d.detail;
      console.log(formatDate(currentDate));
      getAttendance();
    };

    function formatDate(date) {
    var d = new Date(date),
        month = '' + (d.getMonth() + 1),
        day = '' + d.getDate(),
        year = d.getFullYear(); 

    if (month.length < 2) 
        month = '0' + month;
    if (day.length < 2) 
        day = '0' + day;

    return [year, month, day].join('-');
    }

    export let load = false;
    $: if (load === true) {
        console.log("Outer run")
        getAttendance();
    }
    // +formatDate(currentDate);
    $: displayTable = false;

    async function getAttendance() {
        console.log("Inner run")
        let url = "/readEmpAttd?date="+formatDate(currentDate)
        const res = await fetch(url).then(res => res.json()).then(
			parsed => {
                    prows = Object.entries(parsed).map(function(entry){
                        let key = entry[0];
                        let value = entry[1];

                        let nested_object = value;
                        nested_object.key = key;
                        
                        return nested_object;
                    });
                displayTable = true
            }
		);
    }

    async function markAttendance(row) {
        console.log("Inner run")
        console.log(row.attend)
        if(row.attend === true) {
            let url = "/addAttd?emp_id="+row.emp_id+"&date="+formatDate(currentDate)+"&attend="+row.attend
            const res = await fetch(url).then((response) => {
            if (!response.ok){
                throw new Error('Failed');
            }
            })
        }else {
            let url = "/delAttd?emp_id="+row.emp_id+"&date="+formatDate(currentDate)
            const res = await fetch(url).then((response) => {
            if (!response.ok){
                throw new Error('Failed');
            }
            })
        }
    }

    function fillRows (eventMsg)  {
        prows = Object.entries(eventMsg.detail).map(function(entry){
                let key = entry[0]; 
                let value = entry[1];

                let nested_object = value;
                nested_object.key = key;

                return nested_object;
            });
        displayTable = true
    }

    function showTable() {
        
    }

    function onCellClick(row) {
            console.log(row)
            //row.attend = !row.attend;
            //let id = 'cb' + row.emp_id;
            //console.log(id);
            //id.checked = !id.checked;=
            console.log(row.attend)
            displayTable = true;
    }

    function onChecked(passed_row) {
        if(passed_row.attend === false) {
            console.log("deleted")
            //delete the row from database
        } else {
            console.log("inserted")
            //insert the row into database
        }
        console.log("oc")
        console.log(passed_row)
    }


    function onSortString(event) {
        event.detail.rows = sortString(
            event.detail.rows,
            event.detail.dir,
            event.detail.key
        );
    } 

    let attend = false;

    $: prows, console.log(prows)


  </script>
  
  <h1>Attendance</h1>
  <DatePicker
    on:datechange={onDateChange}
    selected={currentDate}
    isAllowed={date => {
      const millisecs = date.getTime();
    //   if (millisecs + 25 * 3600 * 1000 < Date.now()) return false;
      if (millisecs > Date.now()) return false;
      return true;
    }} />
  <div class = 'pad'></div>

  {#if displayTable === true}
    <Table {page} {pageSize} rows={prows}  let:rows={rows2}>
        <thead slot="head">
            <tr>
                <td align="">
                    Employee ID
                    <Sort key="emp_id" on:sort={onSortString} />
                </td>
                <td>
                    First Name
                    <Sort key="first_name" on:sort={onSortString} /> 
                </td>
                <td>
                    Last Name
                    <Sort key="last_name" on:sort={onSortString} /> 
                </td>
                <td>
                    Marked
                    <!-- <Sort key="marked" on:sort={onSortString} />  -->
                </td>
            </tr>
        </thead>
        <tbody>
        {#each rows2 as row,index (row)}
            <tr on:click={() => onCellClick(row)} class='data'>
                <td>{row.emp_id}</td>
                <td>{row.first_name}</td>
                <td>{row.last_name}</td> 
                <td><input type=checkbox bind:checked={row.attend} on:change={markAttendance(row)}/></td>
                <!--row.attend={row.attend == undefined || row.attend == false ? false : true} type=checkbox-->
            </tr>
        {/each}
        </tbody>
    </Table> 
{:else}
<div class="loading">
<Circle color='#FF3E00'></Circle>
</div> 
{/if}


