<script>

import { darkFlag } from './stores/dark.js';
import anime from 'animejs/lib/anime.es.js';

$: if($darkFlag) {
    console.log("Darkened")
    window.document.body.classList.toggle('dark-mode');
    $darkFlag = false;
}

const anim11 =  () => {
    anime.remove('.title_1')
    anime({
        targets: '.title_1',
        translateY:-20
    })
}

const anim12 =  () => {
    anime.remove('.title_1')
    anime({
        targets: '.title_1',
        translateY:5
    })
}


const anim21 =  () => {
    anime.remove('.title_2')
    anime({
        targets: '.title_2',
        translateY:-20
    })
}

const anim22 =  () => {
    anime.remove('.title_2')
    anime({
        targets: '.title_2',
        translateY:5
    })
}


const anim31 =  () => {
    anime.remove('.title_3')
    anime({
        targets: '.title_3',
        translateY:-20
    })
}

const anim32 =  () => {
    anime.remove('.title_3')
    anime({
        targets: '.title_3',
        translateY:5
    })
}

let src = './logo-black.png'

</script>

<div class = 'landing-container'>
    <img {src}>
    <div>
        <h2>Y4 Human Resource Management Service</h2>
        <h6>Zendy Marketing Sdn Bhd</h6>
    </div>
    <div class="welcome">
        <p>YOUR NEW HR SOLUTION</p>
    </div>
    <div class = 'out_container'>
        <div id='col1' class = 'column' style="border-right:2px solid orange;" on:mouseenter={anim11} on:mouseleave={anim12}>
            <p class = 'title title_1'>New Member <br>joined the family?</p>
            <svg xmlns="http://www.w3.org/2000/svg" width="75" height="75" fill="currentColor" class="bi bi-person-plus-fill" viewBox="0 0 16 16">
                <path d="M1 14s-1 0-1-1 1-4 6-4 6 3 6 4-1 1-1 1H1zm5-6a3 3 0 1 0 0-6 3 3 0 0 0 0 6z"/>
                <path fill-rule="evenodd" d="M13.5 5a.5.5 0 0 1 .5.5V7h1.5a.5.5 0 0 1 0 1H14v1.5a.5.5 0 0 1-1 0V8h-1.5a.5.5 0 0 1 0-1H13V5.5a.5.5 0 0 1 .5-.5z"/>
              </svg>
            <p class = 'desc'>Insert the employee's details into database now!</p>
            <button class = 'inner_button' on:click={()=> window.location.href = "/manageEmp/edit"}>Add Employee</button>
        </div>
        <div class = 'column' style="border-right:2px solid orange;" on:mouseenter={anim21} on:mouseleave={anim22}>
            <p class = 'title title_2'>Every employee is a family member in this big family</p>
            <svg xmlns="http://www.w3.org/2000/svg" width="75" height="75" fill="currentColor" class="bi bi-people-fill" viewBox="0 0 16 16">
                <path d="M7 14s-1 0-1-1 1-4 5-4 5 3 5 4-1 1-1 1H7zm4-6a3 3 0 1 0 0-6 3 3 0 0 0 0 6z"/>
                <path fill-rule="evenodd" d="M5.216 14A2.238 2.238 0 0 1 5 13c0-1.355.68-2.75 1.936-3.72A6.325 6.325 0 0 0 5 9c-4 0-5 3-5 4s1 1 1 1h4.216z"/>
                <path d="M4.5 8a2.5 2.5 0 1 0 0-5 2.5 2.5 0 0 0 0 5z"/>
              </svg>
            <p class = 'desc'>Understand every employee more now!</p>
            <button class = 'inner_button' on:click={()=> window.location.href = "/manageEmp"}>View Employee</button>
        </div>
        <div class = 'column' on:mouseenter={anim31} on:mouseleave={anim32}>
            <p class = 'title title_3'>Attendance represents employee's time</p>
            <svg xmlns="http://www.w3.org/2000/svg" width="75" height="75" fill="currentColor" class="bi bi-calendar-check" viewBox="0 0 16 16">
                <path d="M10.854 7.146a.5.5 0 0 1 0 .708l-3 3a.5.5 0 0 1-.708 0l-1.5-1.5a.5.5 0 1 1 .708-.708L7.5 9.793l2.646-2.647a.5.5 0 0 1 .708 0z"/>
                <path d="M3.5 0a.5.5 0 0 1 .5.5V1h8V.5a.5.5 0 0 1 1 0V1h1a2 2 0 0 1 2 2v11a2 2 0 0 1-2 2H2a2 2 0 0 1-2-2V3a2 2 0 0 1 2-2h1V.5a.5.5 0 0 1 .5-.5zM1 4v10a1 1 0 0 0 1 1h12a1 1 0 0 0 1-1V4H1z"/>
              </svg>
            <p class = 'desc'>Mark attendance of employee now!</p>
            <button class = 'inner_button' on:click={()=> window.location.href = "/empAttendance"}>Mark Attendance</button>
        </div>
    </div>
    <div class="line-container"></div>
    <div class="card-container">
        <div class="card text-center">
            <div class="card-body">
                <p class="card-title"><strong>Want to learn more about us?</strong></p>
                <button type="button" class="btn btn-info" on:click={()=> window.location.href = "/about"}>About us</button>
            </div>
        </div>
    </div>
    
</div>

<style>

    .line-container {
        margin: 2vh;
        border-bottom: 4px solid orange;
        width: 90vw;
    }

    .card-container {
        margin-top:3vh;
        width: 400px;
    }

    .card-container .card-body {
        margin-top:2vh;
        margin-bottom:2vh;
    }


    .landing-container {
        display:flex;
        flex-direction: column;
        justify-content: center;        align-items: center;
    }

    .welcome {
        display:flex;
        margin-top: 2vh;
        justify-content: center;
        border-top: 4px solid orange;
        border-bottom: 4px solid orange;
        width: 90vw;
        height : 10vh;
        font-size: 5vh;
        align-items: center;
    }

    .welcome p {
        margin:0;
        font-family: 'Oswald', sans-serif;
    }

    img {
        max-height: 10vh;
    }
    .out_container{
        display: flex;
        margin-top: 2vh;
        clear:both;
        background-color: aliceblue;
        height: 480px;
    }
    .column{
        display: flex;
        flex-direction: column;
        text-align: center;
        width: 33.33%;
        padding:10px;
        float: left;
        justify-content: center;
        align-items: center;
    }

   
    .title{
        display: flex;
        font-size: 24pt;
        font-weight: bolder;
        margin-bottom: 3%;
    }
    .desc{
        margin-top: 10%;
        text-align: center;
    }

    .inner_button {
        display: block;
        width: 150px;
        align-self: center;
        text-align: center;
        background-color: var(--dark-btn);
    }
   
    @media screen and (max-width: 600px) {
  .column {
    width: 100%;
  }
}
</style>