<script>
    import Table, { Pagination, Row, Search, Sort } from "./Table.svelte";
    import { onMount } from 'svelte';
    import {Form, FormGroup, Input,Label ,Button, Modal,
    ModalBody,
    ModalFooter,
    ModalHeader} from 'sveltestrap'
    import { sortNumber, sortString } from "./sorting.js";
    import { Circle } from 'svelte-loading-spinners';

    let page = 0;
    let pageSize = 10;
    let prows = [];
    let selected = null;
    let attendance = false;
    let displayTable = false;
    $: imgURL = "";
    let displayedEmp;

    export let load = false;

    $: if (load === true) {
        console.log("Outer run")
        getEmployee()
    }

    //Modal variables
    let open = false;
    const toggle = () => {
        open = !open;
    }

	async function getEmployee() {
        console.log("Inner run")
        let url = '/readEmp'
        const res = await fetch(url).then(res => res.json()).then(
			parsed => {
                    prows = Object.entries(parsed).map(function(entry){
                        let key = entry[0];
                        let value = entry[1];

                        let nested_object = value;
                        nested_object.key = key;
                        
                        return nested_object;
                    });
                    displayTable = true;
            }
			
		);

    }


    function fillRows (eventMsg)  {
        
    }

    function showTable() {
        
    }

    function onCellClick(row) {
        if(row.quantity !==0) {
            selected = row;
            //toggle()
        }
    }

    function onSortString(event) {
        event.detail.rows = sortString(
            event.detail.rows,
            event.detail.dir,
            event.detail.key
        );
    }

    function editMode(_event){
        window.location.href = "/manageEmp/edit";
    }

    function openModal(emp_id){
        open = true;
        displayedEmp = emp_id
        // createURL();
        console.log(emp_id)
        
        imgURL = "https://y4-bucket.s3.amazonaws.com/emp-id-" + displayedEmp + "_image_file.jpg"
    }

</script>

<img class="editImg" src="edit.png" alt="An edit icon" on:click={editMode} >


{#if displayTable === true}
    <Table {page} {pageSize} rows={prows}  let:rows={rows2}>
        <thead slot="head">
            <tr>
                <td align="">
                    Employee ID
                    <Sort key="emp_Id" on:sort={onSortString} />
                </td>
                <td>
                    First Name
                    <Sort key="first_name" on:sort={onSortString} /> 
                </td>
                <td>
                    Last Name
                    <Sort key="last_name" on:sort={onSortString} /> 
                </td>
                <td>
                    Primary Skill
                    <Sort key="pri_skill" on:sort={onSortString} /> 
                </td>
                <td>
                    Location
                    <Sort key="location" on:sort={onSortString} /> 
                </td>
                <td>
                    Picture
                </td>
            </tr>
        </thead>
        <tbody>
        {#each rows2 as row, index (row)}
            <tr on:click={() => onCellClick(row)} class='data'>
                <td>{row.emp_id}</td>
                <td>{row.first_name}</td>
                <td>{row.last_name}</td> 
                <td>{row.pri_skill}</td>
                <td>{row.location}</td>                
                <td><button on:click={() => {openModal(row.emp_id)}} class="imgBtn" type="button" >Show Image</button></td>
            </tr>
        {/each}
        </tbody>
    </Table> 
{:else}
<div class="loading">
    <Circle color='#FF3E00'></Circle>
</div> 
{/if}


<Modal isOpen={open} {toggle}>
    <ModalHeader> Picture of Employee ... </ModalHeader>
    <ModalBody>
        <div class="displayImg">
            <img src={imgURL} width="200px" height="200px" alt="an employee picture" style="text-align: center;"/>
        </div>
    </ModalBody>
    <ModalFooter>
        <Button color="primary" on:click={toggle}>Done</Button>
    </ModalFooter>
</Modal>

<style>
    /* tr.data:hover {
        background-color: lightcyan !important;
        transition: 0.2s;
    } */

    tr.data {
        background-color: white !important;
        cursor: pointer; 
    }

    tr.base {
        background-color: rgb(245, 245, 245);
    }

    .loading {
        display:flex;
        padding: 1em;
        justify-content: center;
    }

    td{
        text-align: left;
    }


    :global(body.dark-mode) td {
        background-color:  var(--dark-mode-bg) !important;
		color: var(--dark-mode-lines)  !important;
    }
 

    :global(#qtyRange) {
        border:0px !important;
    }

    .editImg{
        height: 5vh;
        margin-top: 1%;
