<script>
    import { Collapse, Navbar,NavbarToggler,NavbarBrand,Nav,NavItem,NavLink} from 'sveltestrap';
    import { darkFlag } from './stores/dark.js';

    let isOpen = false;    
    let navColor = 'light';
    let src = '../logo-black.png'

    function toggleDark() {
        window.document.body.classList.toggle('dark-mode');
        if(!$darkFlag) {
            $darkFlag = true;
            src = "../logo-white.png";
        } else {
            $darkFlag = false;
            src =  '../logo-black.png';
        }
    }


    function handleUpdate(event) {
        isOpen = event.detail.isOpen;
    }
</script>
<Navbar color='{$darkFlag ? 'dark' : 'light'}' light expand="sm">
    <NavbarBrand href="/">
        <img class="logo" {src}>
    </NavbarBrand>
    <NavbarToggler on:click={() => (isOpen = !isOpen)} />
    <Collapse {isOpen} navbar expand="sm" on:update={handleUpdate}>
        <Nav class="ms-auto" navbar color='{navColor}'>
            <NavItem >
                <NavLink href="/" class='header_nav_item'>Home</NavLink>
            </NavItem>
            <NavItem>
                <NavLink href="/about" class='header_nav_item'>About</NavLink>
            </NavItem>
            <NavItem>
                <NavLink href='/manageEmp' class='header_nav_item'>Manage</NavLink>
            </NavItem>
            <NavItem>
                <NavLink href='/empAttendance' class='header_nav_item'>Attendance</NavLink>
            </NavItem>    
            <NavItem>
                <a href="#" on:click={toggleDark}>
                    <svg xmlns="http://www.w3.org/2000/svg" width="16" height="16" fill="currentColor" class="bi bi-moon-fill" viewBox="0 0 16 16">
                        <path d="M6 .278a.768.768 0 0 1 .08.858 7.208 7.208 0 0 0-.878 3.46c0 4.021 3.278 7.277 7.318 7.277.527 0 1.04-.055 1.533-.16a.787.787 0 0 1 .81.316.733.733 0 0 1-.031.893A8.349 8.349 0 0 1 8.344 16C3.734 16 0 12.286 0 7.71 0 4.266 2.114 1.312 5.124.06A.752.752 0 0 1 6 .278z"/>
                    </svg>
                </a>
            </NavItem>
        </Nav>
        </Collapse>   
</Navbar> 

<style>
    .logo {
        margin-left:2%;
        height:70px;
        width:auto;
    }
    a {
        margin-left:1vw;
        margin-right:1vw;
    }


    :global(body.dark-mode .header_nav_item) {
        background-color:  var(--dark-mode-bg) !important;
		color: var(--dark-mode-lines)  !important;
    }
 

</style>